import patterns from './patterns';

console.log('a')

/**
 * try a function, log the error if it throws an exception.
 *
 * @param {Function} f - The function to call.
 */
export function tryCatch(f) {
    try {
        return f();
    } catch (e) {
        // console.log(e);
        gaEvent(
            'jsError',
            'JavaScript error',
            `Line ${e.lineNumber} – ${e.message}`,
            `Pattern launch ${e.fileName} (${window.location})`,
            true
        );
    }
}

/**
 * Runs the function for each pattern against every element that has the
 * matching class, unless the element also has that class with a '-no-js' suffix
 * (for cases where you want to use the CSS for a pattern, but not the
 * functionality).
 *
 * @param {object} pattern - An object containing the class name to trigger the pattern and the launch function to run for those elements.
 */
function launchPattern(pattern) {
    if (typeof pattern === 'function') {
        tryCatch(pattern);
    } else if (pattern.launchQuery) {
        const { launchFn, launchQuery } = pattern;
        Array.from(document.querySelectorAll(launchQuery))
            .filter((elem) => elem.className.indexOf(`${launchQuery}-no-js`))
            .forEach((elem) => tryCatch(() => launchFn(elem)));
    }
}

document.addEventListener(
    'DOMContentLoaded',
    () => {
        if (/(Trident|MSIE)/.test(navigator.userAgent)) {
            return;
        }else{
            patterns.forEach(launchPattern);

            const parameters = parametersToObject(location.search);
            parameters['dev'] &&
                devcorate(
                    document.querySelector('body'),
                    'dev',
                    parameters['dev']
                );
            parameters['rel'] &&
                devcorate(
                    document.querySelector('body'),
                    'rel',
                    parameters['rel']
                );
        }
    },
    false
);